import { Box, Heading, Text, theme } from "@mechatronick/datum-design-system";
import React from "react";
import { FlexWrapperBorderMD } from "./FlexWrapperBorder";
import Fade from "react-reveal/Fade";
import PropsTypes from "prop-types";

const ImageAndTextTab = props => {
  const { data, textAlign, bg } = props;
  return (
    <Fade bottom>
      <FlexWrapperBorderMD
        style={{
          textAlign: textAlign,
          backgroundColor: theme.colors[bg] || bg,
        }}
        maxWidth={`${theme.breakpoints.md}em`}
        wrap={"wrap"}
      >
        <Box
          style={{ alignItems: "center", margin: "auto" }}
          width={[0, 1 / 3, 1 / 5]}
          p={[0, 3, 3]}
        >
          <img
            title={data.name}
            alt={data.name}
            width={"100%"}
            height={"auto"}
            src={data.svg}
          />
        </Box>
        <Box
          style={{ alignItems: "flex-start", margin: "0" }}
          width={[1, 2 / 3, 4 / 5]}
          p={3}
        >
          <Heading.h3
            style={{
              margin: `${theme.space[2]}px 0`,
              color: theme.colors["primary"],
            }}
            bold
          >
            {data.heading}
          </Heading.h3>
          <Text>{data.description}</Text>
          {data.list ? (
            <ul>
              {data.list.map((item, index) => (
                <li
                  key={index}
                  style={{
                    color: theme.colors["primary"],
                  }}
                >
                  {item}
                </li>
              ))}
            </ul>
          ) : null}
        </Box>
      </FlexWrapperBorderMD>
    </Fade>
  );
};

ImageAndTextTab.defaultProps = {
  textAlign: "center",
  alignItems: "center",
  bg: "transparent",
};

ImageAndTextTab.propsTypes = {
  textAlign: PropsTypes.oneOf(["center", "left", "right"]),
  data: PropsTypes.arrayOf(PropsTypes.objectOf(PropsTypes.string)).isRequired,
};

export default ImageAndTextTab;
